.home-page {
    display: flex;
    flex-direction: column;
    gap: rem(10px);
    align-items: center;

    border-radius: rem(10px);
    padding: rem(10px);

    .guest-welcome {
        display: flex;
        flex-direction: column;
        gap: rem(10px);
        align-items: center;
        background-color: $clr2;
        padding: rem(10px);
        border-radius: rem(10px);

        svg {
            background-color: rgba(31, 255, 65, 0.7);
            --main-color: $clr5;
            --hover-color: rgba(31, 255, 65, 1);
            // --active-color: yellow;
        }
        p {
            cursor: pointer;
            color: blue;
        }
        button {
            border-radius: rem(10px);
            padding: rem(10px);
            background-color: $clr6;
        }
    }

    .user-balance {
        height: rem(250px);
        width: rem(250px);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: rem(10px);
        background-color: $clr2;
        padding: rem(10px);
        border-radius: rem(10px);
    }

    &.logged-user {
        text-align: center;
    }

    button {
        width: fit-content;
    }

    .transfers-table {
        position: relative;
        button {
            position: absolute;
            padding: rem(5px);
            right: 0;
            top: 0;
            height: rem(50px);
            width: rem(50px);
            background-color: $clr2;
            border-radius: rem(10px);
        }
    }

    @include for-mobile-layout {
        padding: rem(5px);
        gap: rem(5px);
        .guest-welcome {
            h1 {
                font-size: rem(23px);
            }
        }
        .user-balance {
            height: rem(200px);
            width: rem(200px);
        }
        .transfers-table {
            button {
                height: rem(40px);
                width: rem(40px);
            }
        }
    }
}
