.app-header {
    padding: rem(10px);
    height: rem(80px);
    margin-inline: rem(70px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $clr2;
    border-radius: rem(10px);

    .MuiBox-root {
        display: flex;
        flex-direction: row;
    }
    .home-logo {
        cursor: pointer;
        background-color: $clr2;
        border-radius: 50%;
        img {
            width: rem(40px);
        }
    }
    .header-fullname {
        font-weight: 600;
    }

    .MuiButtonBase-root {
        padding: 0;
    }

    @include for-mobile-layout {
        margin-inline-start: 0;
        margin-inline-end: 0;
        justify-content: flex-start;

        .MuiButtonBase-root {
            margin-left: rem(8px);
        }
        div:last-child {
            margin-inline-start: auto;
            margin-inline-end: rem(5px);
        }

        div:first-child {
            margin-inline-start: rem(5px);
        }

        .home-logo {
            img {
                width: rem(50px);
            }
        }

        .MuiAvatar-root {
            width: rem(50px);
            height: rem(50px);
        }

        img {
            height: rem(50px);
        }
    }
}
