.transfers-table,
.contact-details-page {
    .MuiPaper-root {
        background-color: $clr2;
    }
   

    .amount-plus {
        color: #2bd540;
        font-weight: 600;
    }
    .amount-minus {
        color: red;
    }

    @include for-mobile-layout {
        min-width: rem(350px);
        .MuiTableCell-root {
            min-width: 0px;
        }

        .MuiTable-root {
            min-width: auto;
        }
        margin-inline-start: 0;
        margin-inline-end: 0;
        .MuiTableCell-root {
            padding: rem(3px);
            font-size: rem(7px);
        }
    }
}
